.details {
    width: 640px;
    text-align: left;
}

.feed-order-block {
    display: flex;
    justify-content: left;
    align-items: flex-start;
    flex-direction: column;
    width: 630px;
    margin: 0 auto;
    background: #1c1c21;
    border-radius: 30px;
    padding: 20px;
}

.feed-order-short {
    box-sizing: border-box;
    display: flex;
    margin-top: 15px;
    flex-wrap: wrap;
    justify-content: space-between;
}

.feed-order-short div {
    display: flex;
    align-content: flex-start;
    align-items: center;
}

div.ingredients {
    display: block;
    width: 100%;
    height: 400px;
    overflow: auto;
    margin-top: 20px;
}

div.over {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

div.over div.middle {
    width: 440px;
    text-align: left;
}

div.over .iprice {
    width: 90px;
    justify-content: flex-end;
    margin-right: 5px;
    text-align: right;
}

.date {
    display: block;
    padding-right: 20px;
}

div.ingredient {
    width: 64px;
    height: 64px;
    display: inline-block;
    border-radius: 40px;
    border: double 3px transparent;
    text-align: center;
    line-height: 64px;
    vertical-align: middle;
    top: 0;
    background-image: linear-gradient(#1d1526, #1d1526), radial-gradient(circle at left, #8a26e5, #5742f0);
    background-origin: border-box;
    background-clip: content-box, border-box;
    overflow: hidden;
    margin-right: 10px;
    margin-bottom: 5px;
    margin-top: 5px;
}

.i1 {
}

.ingredient div {
    margin: 0 auto;
}

.ingredient div img {
    display: block;
    margin-left: -27px;
    width: 120px;
}


.sum {
    margin-top: 40px;
    padding-right: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.fullname {
    text-decoration: none;
    display: block;
    text-align: left;
    padding-top: 20px;
    width: 100%;
}

.status {
    text-decoration: none;
    display: block;
    padding-bottom: 0;
    padding-top: 0;
    width: 100%;
}

.nohref a:-webkit-any-link {
    color: #ffffff;
    text-decoration: none;
    cursor: pointer;
}

.cancel {
    color: #aa0000;
}

.done {
    color: #00cccc;
}


.ingredients::-webkit-scrollbar {
    width: 12px;
}

.ingredientsr::-webkit-scrollbar-track {
    background: #1c1c21;
}

.ingredients::-webkit-scrollbar-thumb {
    background: #8585AD;
    border: 0 solid #1c1c21;
    border-radius: 0;
}

